import { useEffect, useRef } from "react";

interface GoogleLoginProps {
  onSuccess: (res: { credential: string }) => void;
  onError: () => void;
}

export function GoogleLogin({ onSuccess, onError }: GoogleLoginProps) {
  const btnRef = useRef<HTMLDivElement | null>(null);
  useEffect(() => {
    const src = "https://accounts.google.com/gsi/client";
    const exists = document.querySelector(`script[src="${src}"]`);
    const load = () => {
      const clientId = process.env.NEXT_PUBLIC_GOOGLE_CLIENT_ID || "";
      if (!(window as any).google || !clientId) {
        onError();
        return;
      }
      if (!(window as any).__gisInitDone) {
        try {
          (window as any).google.accounts.id.disableAutoSelect();
        } catch {}
        (window as any).google.accounts.id.initialize({
          client_id: clientId,
          callback: (response: any) => {
            if (response?.credential)
              onSuccess({ credential: response.credential });
            else onError();
          },
          auto_select: false,
          use_fedcm_for_prompt: false,
        });
        (window as any).__gisInitDone = true;
      }
      if (btnRef.current) {
        (window as any).google.accounts.id.renderButton(btnRef.current, {
          theme: "outline",
          size: "large",
          text: "continue_with",
        });
      }
    };
    if (exists) load();
    else {
      const script = document.createElement("script");
      script.src = src;
      script.async = true;
      script.defer = true;
      script.onload = load;
      document.body.appendChild(script);
    }
  }, [onSuccess, onError]);
  return <div ref={btnRef} className="flex w-full justify-center" />;
}
